*{
	box-sizing:border-box;
}
html,body {
	width:100%;
	height:100%;
	background:black;
}
.nav {
	.block-container {
		width:70%;
		margin: 0 auto;
	}
}

.hero {
	width:100%;
	height:100%;
	display:flex;

	.angel{
		width:50%;
		height:100%;
	}
	.demon {
		width:50%;
		height:100%
	}
	img{
		width:100%;
		height:100%;
	}
	.middle {
	    position: absolute;
	    left: 50%;
	    transform: translate(-50%);
	    height: 400px;
	    width: 400px;
		.angel--button, .demon--button {
			height:100px;
			width:100px;
			padding: 20px 40px;	
			position:relative;
			left:50%;
			transform:translate(-50%);
			margin-top:100px;
		}
		.angel--button {
			background:blue;
		}
		.demon--button {
			background:red;
		}
	}

}


.parallax {
  perspective: 1px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}
.parallax__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.parallax__layer--base {
  transform: translateZ(0);
}
.parallax__layer--back {

}
.diablo {
	background-image:url(../img/diablo.jpg);
    opacity:.5
}
.tyrael {
	background-image:url(../img/TYRAEL.jpg);
	opacity:.5;
}
.parallax__layer--back {
  transform: translateZ(-1px) scale(3);
}

.parallax__group {
  position: relative;
  height: 100vh;
  transform-style: preserve-3d;
}


h1 {
	color:white;font-size:48px;padding-top:25%;
}


